body {
  overflow-x: hidden;
  background-color: black;
}

body::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar {
  width: 5px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #555;
}

* {
  border: 0;
  padding: 0;
  margin: 0;
}

.section {
  font-family: 'Open Sans';
  width: 100%;
  padding: 40px 20px;
}

@media (max-width: 700px) {
  .section {
    padding: 20px 0;
  }
}

.section_bar {
  margin: -5px;
  border-top: 2px solid #000;
}

.section_title {
  font-family: 'Raleway';
  text-align: center;
}

@media (max-width: 500px) {
  .section_title {
    font-size: 1.5rem;
  }
}

.section_body {
  text-align: center;
  font-size: 1.2rem;
  padding: 15px 5px;
}

@media (max-width: 500px) {
  .section_body {
    font-size: 0.9rem;
  }
}

.gofundme_iframe {
  width: 100%;
  overflow: hidden;
}

.gofundme_iframe > * {
  overflow: hidden;
}

.gofundme_iframe-lg {
  height: 530px;
}

@media (max-width: 800px) {
  .gofundme_iframe-lg {
    display: none;
  }
}

.gofundme_iframe-md {
  height: 230px;
}

@media (min-width: 799px) {
  .gofundme_iframe-md {
    display: none;
  }
}

@media (max-width: 800px) {
  .gofundme {
    display: none;
  }
}

.team_con {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: center;
}

.team_member {
  width: 50%;
  box-sizing: border-box;
  padding: 10px;
}

@media (min-width: 900px) {
  .team_member {
    width: 33%;
  }
}

@media (min-width: 1200px) {
  .team_member {
    width: 25%;
  }
}

.team_member-img > img {
  object-fit: cover;
  width: 100%;
  height: 100% !important;
}
